<template>
    <div id="app">
      <router-view/>
    </div>
</template>

<script>
import Nui from './utils/Nui.js'

export default {
  name: 'app',
  components: {
  },
  data() {
    return {
      show: false
    };
  },
  computed: {
      console: () => console,
      window: () => window,
      document: () => document,
   },
  destroyed() {
    window.removeEventListener('message', this.listener);
  },
  mounted() {
    document.addEventListener("keydown", (e) => {
      switch(e.keyCode) {
        case 27:
          this.$root.$emit('close_ui')
        break
      }
    });

    this.listener = window.addEventListener(
      'message',
      (event) => {
        const item = event.data
        Nui.emulate(item, 10)
        switch(item.action) {
          case "start_menu":
            this.$root.$emit('change_data', item.data)
          break
          case "open_menu":
            this.$root.$emit('open_ui')
            this.$root.$emit('change_data_2', item.data)
          break
          case "close_ui":
            this.$root.$emit('close_ui')
          break
        }
      },
      false,
    );
  },
  methods: {
  },
};
</script>