            this.pause.active = false
            Nui.send('closeNui')
        })

        this.$root.$on('change_data_2', (data) => {
            if(data) {
                if(data.inventario) {
                    this.pause.inventario = data.inventario
                }
                if(data.my_data) {
                    this.pause.my_data = data.my_data
                }

                this.pause.config.colore = data.colore || '#36d2c5'
                this.pause.config.nome_server = data.nome_server || 'Nts Roleplay'
                this.pause.config.tasto_esc = data.tasto_esc || true
