                    this.pause.config.langs = data.langs
                }
                if(data.bottoni) {
                    this.pause.config.bottoni = data.bottoni
                }
                if(data.riquadro) {
                    this.pause.config.riquadro_testo = data.riquadro
                }
                if(data.images) {
                    this.pause.images = data.images
                }
                if(data.shop) {
                    this.pause.shop = data.shop
                }
            }
        })
    },
    computed: {
      console: () => console,
      window: () => window,
      document: () => document,
    },
    methods: {
        getArgs(str) {
            return str.trim().split(/ +/);
        },
        numeroPunti(x) {
            try {
                x = x.toString()
            } catch(e) {
                x = 'Non trovato'
            }
            var pattern = /(-?\d+)(\d{3})/;
            while (pattern.test(x))
                x = x.replace(pattern, "$1.$2");
            return x;
        },
        pressButton(name, close) {
            Nui.send('pressButton', {
                name: name,
            })

            if(close) {
                this.pause.active = false
                Nui.send('closeNui')
            }
        },
        goUrl(url) {
            if(url.includes('https://')) {
                this.$nextTick(() => {
                    this.window.invokeNative('openUrl', url)
                })
            }
        },
